.tbContainer {
    height: 60px;
    max-width: 100%;
    min-width: 100%;
    display: flex;
    background-color: blue;
    flex-direction: row;
    align-items: center;
    top: 0;
    position: fixed;
}

.appName {
    flex-grow: 1;
    font-size: 50px;
    padding-left: 10px;
    color: white;
}
.searchIcon{
    color: white;
    font-size: 30px;
    line-height: 60px;
    padding-right: 10px;
}
.search{
    width: 200px;
    border-radius: 20px;
    border: 2px solid white;
    height: 28px;
    padding-left: 10px;
    margin-right: 10px;
}
.hide{
    display: none;
}