.dialog {
  width: 90vw;
  max-width: 20rem;
  
  padding: 1em;
  
  background: #fff;
  color: #656e77;
  box-shadow: 0 0 5px rgba(101, 110, 119, .2);
  border: 0;
  text-align: center;
}

.dialog[open] {
  animation: .2s linear slidein;
}

.dialog::backdrop {
  background: rgba(224, 228, 232, .9);
  animation: .4s ease fade;
}

@keyframes slidein {
  from {
      transform: translate3d(0, 5px, 0);
  }
  to {
      transfrom: translate3d(0, 0, 0);
  }
}

@keyframes fade {
  from {
      opacity: .3;
  }
  to {
      opacity: 1;
  }
}