.tbContainer {
    height: 60px;
    max-width: 100%;
    min-width: 100%;
    display: flex;
    background-color: blue;
    flex-direction: row;
    align-items: center;
    top: 0;
    position: fixed;
}

.appName {
    flex-grow: 1;
    font-size: 50px;
    padding-left: 10px;
    color: white;
}
.searchIcon{
    color: white;
    font-size: 30px;
    line-height: 60px;
    padding-right: 10px;
}
.search{
    width: 200px;
    border-radius: 20px;
    border: 2px solid white;
    height: 28px;
    padding-left: 10px;
    margin-right: 10px;
}
.hide{
    display: none;
}
.dialog {
  width: 90vw;
  max-width: 20rem;
  
  padding: 1em;
  
  background: #fff;
  color: #656e77;
  box-shadow: 0 0 5px rgba(101, 110, 119, .2);
  border: 0;
  text-align: center;
}

.dialog[open] {
  -webkit-animation: .2s linear slidein;
          animation: .2s linear slidein;
}

.dialog::-webkit-backdrop {
  background: rgba(224, 228, 232, .9);
  -webkit-animation: .4s ease fade;
          animation: .4s ease fade;
}

.dialog::backdrop {
  background: rgba(224, 228, 232, .9);
  -webkit-animation: .4s ease fade;
          animation: .4s ease fade;
}

@-webkit-keyframes slidein {
  from {
      -webkit-transform: translate3d(0, 5px, 0);
              transform: translate3d(0, 5px, 0);
  }
  to {
      transfrom: translate3d(0, 0, 0);
  }
}

@keyframes slidein {
  from {
      -webkit-transform: translate3d(0, 5px, 0);
              transform: translate3d(0, 5px, 0);
  }
  to {
      transfrom: translate3d(0, 0, 0);
  }
}

@-webkit-keyframes fade {
  from {
      opacity: .3;
  }
  to {
      opacity: 1;
  }
}

@keyframes fade {
  from {
      opacity: .3;
  }
  to {
      opacity: 1;
  }
}
